<template>
  <div class="form-contain">
    <!--  -->
    <el-form
      size="mini"
      label-width="100px"
      label-position="left"
      @submit.native.prevent="onSubmit"
    >
      <div class="basic-search">
        <el-input
          placeholder=""
          prefix-icon="el-icon-search"
          class="form-search"
          size="medium"
          v-model="form.name"
        >
        </el-input>
        <el-button type="primary" size="medium" class="search-btn" native-type="submit"
          ></el-button
        >
      </div>
      <el-collapse class="advanced-search" accordion v-model="currentPanel">
        <el-collapse-item title="" name="1">
          <el-form-item label="">
            <el-select placeholder="" v-model="form.city">
              <el-option
                v-for="(item, index) in formlabel.cities"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="" v-model="form.department">
              <el-option
                v-for="(item, index) in formlabel.department"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="" v-model="form.type">
              <el-option
                v-for="(item, index) in formlabel.type"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="" v-model="form.level">
              <el-option
                v-for="(item, index) in formlabel.level"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox-group v-model="form.public">
              <el-checkbox
                class="characteristics"
                v-for="(item, index) in formlabel.public"
                :key="index"
                :label="item"
                name="public"
              ></el-checkbox>
            </el-checkbox-group>
            <el-radio-group v-model="form.private">
              <el-radio
                class="characteristics"
                v-for="(item, index) in formlabel.private"
                :key="index"
                :label="item"
                @click.native.stop.prevent="RatioChange(item, $event)"
              ></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-collapse-item>
        <el-collapse-item title="" name="2" :disabled="forbid">
          <ul
            class="infinite-list"
            v-infinite-scroll="load"
            style="overflow: auto"
            infinite-scroll-disabled="disabled"
          >
            <li
              v-for="item in resultsList"
              :key="item.id"
              @click="onClick(item.id, parseFloat(item.lon), parseFloat(item.lat))"
            >
              <ResultItem
                :id="item.id"
                :name="item.name"
                :topschool="item.topschool"
                :topcourse="item.topcourse"
                :postgraduate="item.postgraduate"
                :satisfaction="item.satisfaction"
              ></ResultItem>
            </li>
            <p v-if="loading">...</p>
            <p v-if="noMore"></p>
          </ul>
        </el-collapse-item>
      </el-collapse>
    </el-form>
  </div>
</template>

<script>
import { getSchoolsInfoAPI } from '@/api//schoolsAPI'
import formlabel from '@/components/formlabel.js'
import ResultItem from '@/components/map/QueryBox/ResultItem.vue'
export default {
  components: {
    ResultItem
  },
  data() {
    return {
      formlabel: formlabel,
      // 
      form: {
        name: '',
        city: '',
        department: '',
        type: '',
        level: '',
        public: [],
        private: ''
      },
      limit: 10,
      offset: 0,
      // 
      currentPanel: '',
      currentItem: {},
      // 
      loading: false,
      noMore: false,
      resultsList: [],
      forbid: true,
      iscreated: false
    }
  },
  computed: {
    disabled() {
      return this.noMore || this.loading
    }
  },
  methods: {
    // 
    RatioChange(val, e) {
      val === this.form.private ? (this.form.private = '') : (this.form.private = val)
    },
    // 
    // ajax
    // 1.
    // 2.
    async onSubmit() {
      // 
      this.forbid = false
      // 
      try {
        const { data: res } = await getSchoolsInfoAPI(this.form)
        if (res.status !== 0) {
          return this.$mynotify.warning(res.message)
        }
        // 
        this.$emit('show-uniPoints', res.data)
        // 
        this.initQueryRes()
      } catch (error) {
        this.$mynotify.error(error.message)
      }

      // BUG1:
      setTimeout(() => {
        this.currentPanel = '2'
      }, 500)
    },

    // 
    async initSchoolsInfoByOffset(begin) {
      this.loading = true
      // 
      const formCopy = {
        ...this.form,
        limit: this.limit,
        offset: this.offset
      }
      const { data: res } = await getSchoolsInfoAPI(formCopy)
      // 
      if (begin) {
        this.resultsList = res.data
      } else {
        // 
        this.resultsList = [...this.resultsList, ...res.data]
      }
      this.loading = false
      if (res.total === 0) {
        this.noMore = true
      }
    },
    // 
    async initQueryRes() {
      this.noMore = false
      this.offset = 0
      this.initSchoolsInfoByOffset(true)
    },
    // 
    async load() {
      this.offset += this.limit
      this.initSchoolsInfoByOffset()
    },
    // item
    onClick(id, lon, lat) {
      // click-item
      this.currentItem.id = id
      this.currentItem.lon = lon
      this.currentItem.lat = lat
      this.$emit('click-item', {
        id,
        lon,
        lat
      })
    }
  }
}
</script>

